import React, {useState, useContext, useEffect, useRef} from 'react';
import logo from './logo.svg';
import './App.css';
import {Map, MapMarker} from 'react-kakao-maps-sdk';

export default App;

function App() {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [data, setData] = useState(null);

  const [searchYearCd, setSearchYearCd] = useState(2015);

  const serviceKey = `ej1Ks%2BJlOlaxMbo9WNIR55NnUjmFV18BiRarUFY06EoWjNSfRk9ilRtcjvc31ixBg0LgZmw%2BLmtOA2AUCe%2BRIw%3D%3D`;
  const url = `https://apis.data.go.kr/B552061/frequentzoneBicycle/getRestFrequentzoneBicycle?serviceKey=${serviceKey}&searchYearCd=${searchYearCd}&siDo=11&guGun=110&type=json&numOfRows=10&pageNo=1`;

  useEffect(() => {  
    setIsLoaded(false);

    fetch(url)
    .then(res => {
      if (!res.ok) {
        throw res;
      }
      return res.json();
    })
    .then(data => {
      setData(data);
    })
    .catch(error => {
      setError(error)
    })
    .finally(() => setIsLoaded(true))
  }, [searchYearCd])

  console.log(data);

  if (error) {
    return <p>failed to fetch</p>
  }
  if (!isLoaded) {
    return <p>fetching data...</p>
  }
  return (
    <>
      <h1>App</h1>

      <div>
        <h2>Year</h2>
        <p>{searchYearCd}</p>
        <button onClick={() => setSearchYearCd(searchYearCd - 1)}>Prev</button>
        <button onClick={() => setSearchYearCd(searchYearCd + 1)}>Next</button>
      </div>

      <div>
        <h2>Result</h2>
        <ul>
          {data.items.item.map((item, index) => (
            <li key={index}>{item.spot_nm}</li>
          ))}
        </ul>
        <C />
      </div>
    </>  
  )
}

function C(){
  return (
    <Map // 지도를 표시할 Container
      center={{
        // 지도의 중심좌표
        lat: 33.450701,
        lng: 126.570667,
      }}
      style={{
        // 지도의 크기
        width: "100%",
        height: "450px",
      }}
      level={3} // 지도의 확대 레벨
    />
  );
}